.progress {
   width: 100%;
   height: 6px;
   position: fixed;
   z-index: 100000;
   top: 0;
   left: 0;
}

.progress > div {
   /* height: px; */
   height: 6px;
   background-color: #000;
   width: 0%;
   box-shadow: 0px 1px 2px #000;
}
@media only screen and (max-width: 600px) {
   .progress {
      display: none;
   }
}
