body a,
html a {
   text-decoration: none;
}

.container000 {
   position: fixed;
   width: 98%;
   top: 1rem;
   text-align: right;
   margin-top: 1rem;
   margin-right: 1rem;
   z-index: 100;
}
#open {
   transition: width 0.5s, height 0.5s, transform 0.5s;
}
#open:hover {
   transform: rotate(180deg) scale(1.2);
   color: red;
}
#close {
   transition: width 0.5s, height 0.5s, transform 0.5s;
}
#close:hover {
   transform: rotate(180deg) scale(1.2);
   color: red;
}

.container000 .menu {
   cursor: pointer;
}
.log {
   width: 2.5rem;
   height: 2.5rem;
   position: absolute;
   top: 1.7rem;
   left: 2rem;
   z-index: 1000;
   transition: width 0.5s, height 0.5s, transform 0.5s;
}
.log .scroll {
   transform: scale(1.2);
}
.over-lay {
   position: absolute;
   right: 0;
   top: -100%;
   padding: 3em;
   overflow: hidden;
   width: 100%;
   height: 100%;
   transform-origin: top;
   opacity: 1;
   z-index: 10000;
   background: #ff9700;
   transition: all 0.6s ease-in-out;
}
.over-lay .cross {
   float: right;
   cursor: pointer;
}
.over-lay ul {
   display: flex;
   justify-content: space-evenly;
   margin-top: 45vh;
}
.over-lay ul li {
   list-style: none;
   opacity: 0;
   transition: all 0.5s;
   font-weight: 700;
}
.over-lay ul li a {
   font-size: 3rem;
   color: #094b3f;
   transition: all 0.3s;
}
.over-lay ul li a:hover {
   color: Cornsilk;
}
.over-lay ul li span {
   display: block;
   margin: 10px 0;
   font-weight: 300;
   color: white;
   font-size: 1.2em;
}

.show {
   transition: all 0.6s ease-in-out;
   display: block;
   opacity: 1;
   visibility: visible;
   top: 0;
}
.show li:nth-of-type(1) {
   animation: show-list 0.8s forwards 1s ease-out;
}
.show li:nth-of-type(2) {
   animation: show-list 0.8s forwards 1.2s ease-out;
}
.show li:nth-of-type(3) {
   animation: show-list 0.8s forwards 1.4s ease-out;
}
.show li:nth-of-type(4) {
   animation: show-list 0.8s forwards 1.6s ease-out;
}

@keyframes show-list {
   0% {
      opacity: 0;
      transform: translateY(200px);
   }
   100% {
      transform: translateY(0);
      opacity: 1;
   }
}
@media only screen and (max-width: 600px) {
   .container000 {
      position: absolute;
      top: 0.5rem;
      right: 0.8rem;
      width: 100%;
   }
   .over-lay ul {
      display: block;
      margin-top: 30%;
   }
   #lin {
      padding-left: 0 !important;
   }
   .over-lay ul li {
      /* list-style: none; */
      opacity: 0;
      text-align: center;
      margin-right: 20px;
      margin-bottom: 40px;
      transition: all 0.5s;
      font-weight: 700;
   }
   #open {
      transform: scale(0.8);
   }
   #open {
      transition: width 0.5s, height 0.5s, transform 0.5s;
   }
   #open:hover {
      transform: rotate(180deg) scale(0.9);
      color: red;
   }
   .log {
      width: 3.22rem;
      height: 3.22rem;
      position: absolute;
      top: 1.8rem;
      left: 2rem;
      z-index: 1000;
      transition: width 0.5s, height 0.5s, transform 0.5s;
   }
}
