@import url("https://fonts.googleapis.com/css?family=Open+Sans");
* {
   box-sizing: border-box;
}
@media only screen and (max-width: 800px) {
   html,
   body {
      font-size: 12px;
      scroll-behavior: smooth;
   }
}
@media only screen and (max-width: 600px) {
   html,
   body {
      font-family: "IBM Plex Sans", sans-serif;
      font-size: 9px;
      overflow-x: hidden;
      overflow-y: scroll !important;
      scroll-behavior: smooth;
      cursor: pointer;
   }
   h1 {
      font-size: 2px;
   }
   .section-heading {
      font-size: 2.5rem !important;
   }
}

body {
   padding: 0px;
   background-color: #f5f5f5;
   font-family: "Roboto", sans-serif;
   line-height: 1.5;
   scroll-behavior: smooth;
   color: #000;
   cursor: none;
   overflow-x: hidden;
   font-weight: 100;
   -webkit-font-smoothing: antialiased;
   -webkit-text-size-adjust: 100%;
}
h1 {
   font-family: "IBM Plex Sans", sans-serif;

   font-size: 4rem;
   color: #000;
   /* width: 100%; */
   font-weight: 100;
   text-align: center;
   padding: 0%;
}
h2 {
   font-size: 3rem;
   font-family: "IBM Plex Sans", sans-serif;
}
p {
   /* font-family: "IBM Plex Sans", sans-serif; */
   font-family: "Roboto", sans-serif;
   color: #000;
   font-size: 2rem;
}
label {
   font-family: "IBM Plex Sans", sans-serif;
}

.page {
   position: absolute;
   left: 15px;
   right: 15px;
}

.page-enter {
   opacity: 0;
   transform: scale(1.1);
}

.page-enter-active {
   opacity: 1;
   transform: scale(1);
   transition: opacity 300ms, transform 300ms;
}

.page-exit {
   opacity: 1;
   transform: scale(1);
}

.page-exit-active {
   opacity: 0;

   transform: scale(0.9);
   transition: opacity 300ms, transform 300ms;
   display: none;
}
